.mppBody {
//Responsive- Small screens
   
  
    .lightHeader {
        @include lightMppHeaders;
        
    }

    .darkHeader {
        @include darkMppHeaders;
    }
    
    .lightSubHeader{
        @include lightMppSubHeaders;
    }

    .darkSubHeader {
        @include darkMppSubHeaders;
    }

    .content {
        @include content;
    }

    .boldContent {
        @include boldContent;
    }

    .lightContent {
        @include lightMppContent;
    }

    .center {
        @include center;
    }


    .mppContent1 { 
 
        background-color: $white;
        color: black;

        .header-wrapper {
            margin-top: 10%;
      
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: repeat(auto-fit(2, 1fr));
            grid-column-gap: 0px;
            grid-row-gap: 0px;
            .div1 { 
                grid-area: 2 / 1 / 3 / 2; 

                .headerGraphic {
                    width: 100%;
                }
            }
            .div2 { 
                grid-area: 1 / 1 / 2 / 2; 
                text-align: center;
                .headerTitle {
                    font-weight: bold;
                    font-size: 80px;
                    text-align: center;
                }
                ul {list-style-type: none;
                    // font-weight: bold;
                    margin: 0;
                    padding:0;
                    margin-top: 5%;
                    font-size: 30px;
                    text-align: center
                 }
            }
    }
        
        .wrapper-1 {
            margin-left: 15%;
            margin-right: 15%;
            // margin-top: 5%;
     
            margin-top: 10%;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: repeat(auto-fit(2, 1fr));
            grid-column-gap: 0px;
            grid-row-gap: 0px;
            .div1{
       
                grid-area: 1 / 1 / 2 / 2; 
                ul {list-style-type: none;
                   font-weight: bold;
                   margin: 0;
                   padding:0;
                   margin-top: 5%;
                }
                }
            .div2 {
                margin-top: 10%;
                grid-area: 2 / 1 / 3 / 2; 
            
            .marginDiv {
                margin-top: 10%;
            }}
        }




        .wrapper-2 {
            margin-left: 15%;
            margin-right: 15%;
            margin-top: 10%;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: repeat(auto-fit(.25, 1fr));
            grid-column-gap: 0px;
            grid-row-gap: 0px;
    
    
    .div1 { grid-area: 1 / 1 / 2 / 2; }
    .div2 { grid-area: 2 / 1 / 3 / 2; 
        // margin-left: 10%;
        margin-top: 5%;
    }
        } }

    .mppContent2 {
        background-color: $mpp-dark;
        padding: 10%;
        margin-top: 10%;
        .wrapper-4 {
            margin-left: 5%;
            margin-right: 5%;
            // margin-top: 5%;

            margin-top: 0%;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: repeat(auto-fit(.25, 1fr));
            grid-column-gap: 0px;
            grid-row-gap: 0px;

            .div1 {
                grid-area: 1 / 1 / 2 / 2;
            }
            .div2 {
                grid-area: 2 / 1 / 3 / 2; 
                .p1 {
                    font-style: italic;
                }
                .p2 {
                    font-weight: bold;
                    font-size: 22px;
                    color: $mpp-light;
                }

                .parent-1 {
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-template-rows: repeat(auto-fit(5, 1fr));
                    grid-column-gap: 0px;
                    grid-row-gap: 0px;
                    
                    
                    .div1 { grid-area: 1 / 1 / 2 / 2; 
                    .mpp1 {width: 100%;}}
                    .div2 { grid-area: 2 / 1 / 3 / 2; 
                        .mpp2 {width: 100%;}}
                    .div3 { grid-area: 3 / 1 / 4 / 2; 
                        .mpp3 {width: 100%;}}
                    .div4 { grid-area: 4 / 1 / 5 / 2; 
                        .mpp4 {width: 100%;}}
                    .div5 { grid-area: 5 / 1 / 6 / 2; 
                        .mpp5 {width: 100%;}}
                }
            }

        }
    }

.mppContent3 {
    background-color: $white;
    color: black;
    .wrapper-3 {
        margin-left: 15%;
        margin-right: 15%;
        margin-top: 10%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(auto-fit(.25, 1fr));
        grid-column-gap: 0px;
        grid-row-gap: 0px;

       .div1{grid-area: 1 / 1 / 2 / 2;}
       .div2 {grid-area: 2 / 1 / 3 / 2; }
       padding-bottom: 20%;;
    }
        

}

}






